.nav {
  display: flex;
  flex-direction: row;
  color: #1f2960;
  min-width: 10rem;
  width: 100%;
}
.ph img {
  margin: 0.5rem;
  margin-left: 3vh;
  max-width: 35rem;
}
.pages {
  display: flex;
  flex-direction: row;
  margin-right: 2rem;
  justify-content: flex-end;
  /* margin-left: 33rem; */
  width: 100%;
}
.button {
  max-width: 9rem;

  width: 100%;
  height: 2.75rem;
  outline: none;
  border: none;
  border-radius: 10px;
  margin: 9px;
  font-family: "Lato";
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 29px;
  color: #1f2960;
  background-color: inherit;
  cursor: pointer;
  margin-left: 2rem;
}
.navbar {
  overflow: hidden;
  background-color: #333;
}

.navbar a {
  float: left;
  font-size: 10px;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

.dropdown {
  float: left;
  overflow: hidden;
}

.dropdown .dropbtn {
  font-size: 16px;
  border: none;
  outline: none;
  color: white;
  padding: 14px 16px;
  background-color: inherit;
  font-family: inherit;
  margin: 0;
}

.navbar a:hover,
.dropdown:hover .dropbtn {
  background-color: red;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.dropdown-content a {
  float: none;
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  text-align: left;
}

.dropdown-content a:hover {
  background-color: #ddd;
}

.dropdown:hover .dropdown-content {
  display: block;
}

