.s {
  position: absolute;
  z-index: 2;
  font-size: 1.5rem;
  color: black;
  background-color: rgb(211, 211, 203);
  width: 17rem;
}
.s div {
  margin-left: 0.3rem;
  padding: 1rem 0 1rem 0;
  height: 2rem;
  cursor: pointer;
}
.s div:hover {
  background-color: rgb(200, 200, 188);
}
.pages div {
  text-decoration: none;
  color: #1f2960;
}
.pages div :link {
  text-decoration: none;
  color: #1f2960;
}
.k {
  width: 3rem;
  height: 3rem;
  display: flex;
  flex-direction: row;
  margin-right: 3rem;
  margin-top: 0.2rem;
}
.k img {
  width: 2rem;
  height: 2rem;
}
.ph {
  width: 9rem;
  height: 4rem;
}
.btn {
  display: none;
}
@media only screen and (max-width: 56.25rem) {
  * {
    margin: 0;
  }
  .pages {
    display: none;
    background-color: gray;
    min-height: 56.25rem;
  }
  .k {
    display: flex;
    flex-direction: row;
  }
}
.k {
  width: 1rem;
  height: 1rem;
  display: none;
  flex-direction: row;
  margin-left: 2rem;
  margin-top: 0.2rem;
}
.k img {
  width: 1rem;
  height: 1rem;
}
.ph {
  width: 8rem;
  height: 2.5rem;
  margin-bottom: 0.5rem;
}
.ph img {
  width: 100%;
  height: 100%;
}
@media screen and (max-width: 930px) {
  .pages {
    margin-left: 2rem;
  }
  .button {
    max-width: 9rem;

    width: 80%;
    height: 2rem;
    outline: none;
    border: none;
    border-radius: 10px;
    margin: 9px;
    font-family: "Lato";
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 29px;
    color: #1f2960;
    background-color: inherit;
    cursor: pointer;
    margin-left: 2rem;
  }
